.welcome {
  background-color: rgba(244, 248, 250, 1);
  position: relative;
  overflow: hidden;
}
.welcome_wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 128px;
  padding-bottom: 144px;
  z-index: 1;
  gap: 10px;
}
.right_part {
  max-width: 576px;
  gap: 56px;
  display: flex;
  flex-direction: column;
}
.right_part_h1 {
  color: rgba(38, 41, 49, 1);
  font-family: RF Dewi Extended;
  font-size: 56px;
  font-weight: 400;
  line-height: 67px;
  letter-spacing: 0%;
  text-align: left;
  & span {
    color: rgba(1, 118, 255, 1);
    font-family: RF Dewi Extended;
    font-size: 56px;
    font-weight: 400;
    line-height: 67px;
    letter-spacing: 0%;
    text-align: left;
  }
}
.right_part_text {
  color: rgba(97, 99, 109, 1);
  font-family: Inter;
  font-size: 16px;
  font-weight: 400;
  line-height: 160%;
  letter-spacing: 0%;
  text-align: left;
  & b {
    color: rgba(63, 65, 74, 1);
  }
}
.right_part_button {
  color: rgba(255, 255, 255, 1);

  font-family: Inter;
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: 0%;
  text-align: left;
  padding: 16px 32px;
  background-color: rgba(1, 118, 255, 1);
  border-radius: 72px;
  max-width: 202px;
}

.left_part {
  max-width: 485px;
  background-color: rgba(255, 255, 255, 1);
  box-shadow: 0px 12px 32px 0px rgba(50, 73, 85, 0.1);
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 40px 48px;
  border-radius: 30px;
  z-index: 1;
  gap: 40px;
}
.login {
  color: rgba(38, 41, 49, 1);

  font-family: RF Dewi Extended;
  font-size: 18px;
  font-weight: 400;
  line-height: 22px;
  letter-spacing: 0%;
  text-align: center;
}
.password_div {
  margin-top: 24px;
  display: flex;
  justify-content: space-between;
  position: relative;
}
.textbox_password {
  color: rgba(169, 171, 182, 1);
  font-family: Inter;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  letter-spacing: 0px;
  text-align: left;
  width: 100%;
  max-width: 389px;
  padding: 16px 8px 16px 20px;
  border: 1px solid rgba(196, 199, 207, 1);
  border-radius: 30px;
  background: rgba(255, 255, 255, 1);
}
.textbox_login {
  width: 100%;
  max-width: 389px;
  padding: 16px 8px 16px 20px;
  border: 1px solid rgba(196, 199, 207, 1);
  border-radius: 30px;
  background: rgba(255, 255, 255, 1);
  color: rgba(169, 171, 182, 1);
  font-family: Inter;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  letter-spacing: 0px;
  text-align: left;
}
.textbox_image_eye {
  position: absolute;
  transform: translateY(-50%);
  top: 50%;
  right: 20px;
  background: none;
}
.recover_password {
  color: rgba(159, 161, 173, 1);
  font-family: Inter;
  font-size: 16px;
  font-weight: 400;
  line-height: 19px;
  letter-spacing: 0%;
  text-align: left;
  margin-top: 8px;
  padding-left: 8px;
  cursor: pointer;
}
.left_part_block_third {
  display: flex;
  flex-direction: column;
  gap: 16px;
}
.button_login {
  padding: 16px 40px;
  border: 1px solid rgba(1, 118, 255, 1);
  border-radius: 72px;
  background-color: rgba(255, 255, 255, 1);
  max-width: 128px;
  align-self: center;
  color: rgba(38, 41, 49, 1);
  font-family: Inter;
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: 0%;
  text-align: left;
  align-self: center;
}
.no_account {
  font-family: Inter;
  font-size: 16px;
  font-weight: 400;
  line-height: 19px;
  letter-spacing: 0%;
  text-align: left;
  color: rgba(159, 161, 173, 1);
  align-self: center;
}
.no_account_register {
  color: rgba(1, 118, 255, 1);
  cursor: pointer;
}
.bg_shapes {
  position: absolute;
  top: 73px;
  left: 646px;
  z-index: 0;
}
@media (max-width: 992px) {
  .container {
    max-width: 768px;
    width: 100%;
    margin: 0 auto;
    padding: 0 12px;
  }
  .left_part {
    flex: 1 1 414px;
    padding: 18px 12px;
    gap: 18px;
    border-radius: 20px;
    box-shadow: 0px 8px 24px 0px rgba(50, 73, 85, 0.1);
    background: rgba(255, 255, 255, 1);
    width: 100%;
  }
  .bg_shapes {
    display: none;
  }
  .right_part_h1 {
    font-family: RF Dewi Extended;
    font-size: 24px;
    font-weight: 400;
    line-height: 29px;
    letter-spacing: 0%;
    text-align: left;
    max-width: 314px;
  }
  .right_part_h1 span {
    font-family: RF Dewi Extended;
    font-size: 24px;
    font-weight: 400;
    line-height: 29px;
    letter-spacing: 0%;
    text-align: left;
  }
  .welcome_wrapper {
    padding: 57px 0 64px;
    gap: 40px;
    justify-content: center;
    align-items: flex-start;
  }
  .right_part_text {
    max-width: 314px;
    font-size: 14px;
  }
  .right_part {
    gap: 24px;
  }
  .textbox_password {
    max-width: none;
    font-size: 14px;
    padding: 12px 8px 12px 20px;
  }
  .textbox_login {
    max-width: none;
    font-size: 14px;
    padding: 12px 8px 12px 20px;
    color: rgb(0, 38, 255);
  }
  .login {
    font-size: 16px;
  }
  .recover_password {
    font-size: 14px;
  }
  .button_login {
    max-width: none;
    width: 100%;
    text-align: center;
    padding: 12px 0;
    font-size: 14px;
  }
  .right_part_button {
    max-width: none;
    width: 100%;
    text-align: center;
    padding: 12px 0;
    font-size: 14px;
  }
  .no_account {
    font-size: 14px;
  }
  .password_div {
    margin-top: 12px;
  }
}
@media (max-width: 584px){
  .about_project_wrapper{
    flex-wrap: wrap;
  }

}